.align {
  display: flex;
  align-items: center; // (default)
  justify-content: space-between; // (default)
  width: 100%;
  .wrap{
    flex-wrap: wrap;
  }
  // align       
  &.column {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: space-between;
  }
  //    :: start / center / end
  &.center {
    justify-content: center;
  }
  &.start {
    justify-content: flex-start;
  }
  &.end {
    justify-content: flex-end;
  }

  //    :: bottom / top
  &.middle {
    align-items: center;
  }
  &.bottom {
    align-items: flex-end;
  }
  &.top {
    align-items: flex-start;
  }
  //  
  .item {
    // text-align: center;
    align-items: center;
    display: inline-flex;
    //     10px (gap )
    & + .item {
      margin-left: 10px;
    }
  }
  // align    (    )
  .lft {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex: 1;
  }
  .rgt {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex: 1;
  }
}

//  
.txt_lft {
  text-align: left;
}
.txt_center{
  text-align: center;
}
.txt_rgt{
  text-align: right;
}

.vertical_top{
  vertical-align: top;
}